import { Component, OnInit, AfterViewInit, Input, Output, EventEmitter } from '@angular/core';
declare var jquery: any;
declare var $: any;
declare var Materialize: any;

@Component({
  selector: 'app-action-btns',
  templateUrl: './action-btns.component.html',
  styleUrls: ['./action-btns.component.css']
})
export class ActionBtnsComponent implements OnInit, AfterViewInit {
  @Input('form') form;
  @Output('onDemo') onDemo: EventEmitter<any> = new EventEmitter();

  constructor() { }

  ngOnInit() {
  }

  ngAfterViewInit() {
    $('.modal').modal();
  }

  onCopy() {
    const el = document.createElement('textarea');
    const f = this.form;
    el.value = `${location.origin}?funcByX=${f.funcByX}&funcByY=${f.funcByY}&intervalOne=${f.intervalOne}&intervalTwo=${f.intervalTwo}`;
    document.body.appendChild(el);
    el.select();
    document.execCommand('copy');
    document.body.removeChild(el);
    Materialize.toast('Ссилку скопіювано в буфер обміну!', 1500, 'rounded deep-orange lighten-2');
  }


  onInstruction() {
    this.onDemo.emit();
  }

}


