import { Component, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { CalculatorService } from '../services/calculator.service';
import mathjs from 'mathjs';

declare var jquery: any;
declare var $: any;
declare var introJs: any;

interface FuncionsEval {
  funcByX: any;
  funcByY: any;
};

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})
export class MainComponent {
  @ViewChild('f') f: NgForm;
  form = {
    funcByX: '2*x-y^2-3*y+8',
    funcByY: 'x^2+2*x-y^2-3.8',
    intervalOne: '0,4',
    intervalTwo: '0,4'
  };
  intervalArrayOne;
  intervalArrayTwo;
  generated: FuncionsEval;
  tableOne: any;
  tableTwo: any;
  result: any;

  constructor(
    private calcService: CalculatorService,
    private route: ActivatedRoute
  ) {
    const queryParams = this.route.snapshot.queryParams;

    if (queryParams.funcByX && queryParams.funcByY && queryParams.intervalOne && queryParams.intervalTwo) {
      this.form = {
        funcByX: queryParams.funcByX,
        funcByY: queryParams.funcByY,
        intervalOne: queryParams.intervalOne,
        intervalTwo: queryParams.intervalTwo
      };
    }
  }

  private createEvalFuncion(
    funcByX: string,
    funcByY: string
  ): FuncionsEval {
    return {
      funcByX: this.calcService.generateFunction(funcByX),
      funcByY: this.calcService.generateFunction(funcByY)
    };
  }

  getInterval(interval) {
    const arr = interval.split(',');
    return arr.map(el => el * 1);
  }

  private getIntervalArray(interval) {
    const arr = this.getInterval(interval);
    const res = [];
    for (let i = arr[0]; i <= arr[1]; i++) {
      res.push(i);
    }
    return res;
  }

  onShowDemo() {
    this.form = {
      funcByX: '2*x-y^2-3*y+8',
      funcByY: 'x^2+2*x-y^2-3.8',
      intervalOne: '0,4',
      intervalTwo: '0,4'
    };

    this.onCalculate();
    introJs().start();
  }

  onCalculate() {
    const controls = this.form;

    if (this.f.valid) {
      this.generated = this.createEvalFuncion(controls.funcByX, controls.funcByY);
      this.intervalArrayOne = this.getIntervalArray(controls.intervalOne);
      this.intervalArrayTwo = this.getIntervalArray(controls.intervalTwo);

      this.tableOne = this.calcService.generateTable(
        this.generated.funcByX,
        this.getInterval(controls.intervalOne)
      );

      this.tableTwo = this.calcService.generateTable(
        this.generated.funcByY,
        this.getInterval(controls.intervalTwo)
      );

      const square = this.calcService.calcSquare(
        this.getInterval(controls.intervalOne),
        this.getInterval(controls.intervalTwo), 
      );

      this.result = {
        min1: this.calcService.getMinMax(this.tableOne),
        max1: this.calcService.getMaxMax(this.tableOne),
        max2: this.calcService.getMaxMax(this.tableTwo),
        min2: this.calcService.getMinMax(this.tableTwo),
        defint: this.calcService.calcDefint(
          controls.funcByX,
          controls.funcByY,
          this.getInterval(controls.intervalOne),
          this.getInterval(controls.intervalTwo),
          square
        )
      };
    }
  }
}



